.standard {
  margin: 0 auto;
  width: 26px;
  height: 43px;
  top: 45%;
  position: absolute;

  .img {
    width: 100%;
  }
}

.prev_btn {
  left: 20px;
  right: auto;
  z-index: 2;
  cursor: pointer;
}

.next_btn {
  right: 20px;
  left: auto;
  z-index: 2;
  cursor: pointer;
}

.disabled {
  pointer-events: none;
  opacity: 0.4;
}
