.pjt_section {
  margin: 100px 10px;
  h1 {
    font-style: italic;
    font-size: 6rem;
    font-weight: 900;
  }
  .text_section {
    display: flex;
    flex-wrap: wrap;
  }
}

// text 영역

.ts_section {
  width: 100%;
  margin: 15px 10px;
  display: flex;
  flex-wrap: wrap;

  .ts_title {
    width: 15vw;
    font-style: italic;
    font-size: 2.5vw;
    font-weight: 900;
  }
  .ts_content {
    width: 80vw;
    font-size: 2vw;
    font-weight: 900;
  }
}

//img 영역

.pjt_img {
  width: 90%;
  margin: 100px auto;
  position: relative;
  border: 2px solid black;
  overflow: hidden;
  box-sizing: border-box;

  ul {
    padding: 0;
    width: 400%;
    list-style: none;
    font-size: 0;
    display: flex;
    transition: all ease 1s;

    .img_section {
      width: 25%;
      margin: 0;

      img {
        width: 100%;
        object-fit: scale-down;
      }
    }
  }
}
