.translateX {
  animation: moveX 1.5s ease-in-out;
  transition-delay: 0s;
}

@keyframes moveX {
  from {
    opacity: 0;
    transform: translateX(-15%);
  }
  to {
    opacity: 100;
    transform: translateX(0);
  }
}

.translateY {
  animation: moveY 1.5s ease-in-out;
  transition-delay: 0s;
}

@keyframes moveY {
  from {
    opacity: 0;
    transform: translateY(15%);
  }
  to {
    opacity: 100;
    transform: translateY(0);
  }
}

.none {
  visibility: hidden;
}
