.main_section {
  position: relative;
  img {
    width: 100%;
  }
  .position {
    position: absolute;
    top: 30%;
    left: 20%;
    overflow: hidden;

    h1 {
      font-style: italic;
      font-weight: bold;
    }

    p {
      font-style: italic;
      font-weight: bold;
    }

    .head_1 {
      font-size: 3vw;
    }
    .head_2 {
      display: flex;
      align-items: baseline;

      .head_3 {
        font-size: 4vw;
        color: #214832;
        text-decoration: underline;
        text-underline-offset: 8px;
      }
      p {
        font-size: 2vw;
      }
    }
    .head_4 {
      font-size: 1.5vw;

      p {
        margin: 0;
      }
    }
  }
}
