ul {
  list-style: none;
}

.header_section {
  top: 0;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 60px;
  background-color: rgba(255, 255, 255, 0.5);
  position: fixed;
  z-index: 999;

  .header_sub {
    margin: 0 100px;
    height: 60px;
    display: flex;
    align-items: center;
    font-weight: 900;
    font-size: 2rem;
    justify-content: space-between;

    .header_logo {
      margin: 0;
      font: inherit;
      font-style: italic;

      &:hover {
        color: #214832;
        font-size: 2.01rem;
        cursor: pointer;
      }
    }

    ul {
      display: flex;
      li {
        padding-left: 50px;
        font-size: 1.5rem;

        &:hover {
          color: #214832;
          font-size: 1.52rem;
          cursor: pointer;
        }
      }
    }
  }
}
