ul {
  list-style: none;
}

.ph_section {
  top: 0;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 60px;
  background-color: rgba(0, 0, 0, 1);
  position: fixed;
  z-index: 999;

  .ph_sub {
    margin: 0 100px;
    height: 60px;
    display: flex;
    align-items: center;
    font-weight: 900;
    font-size: 2rem;
    justify-content: space-between;

    .ph_logo {
      margin: 0;
      font: inherit;
      font-style: italic;
      a {
        &:hover {
          color: lightgray;
          font-size: 2.01rem;
          cursor: pointer;
        }
      }
    }
  }

  ul {
    display: flex;
    a {
      padding-left: 50px;
      font-size: 1.5rem;

      &:hover {
        color: lightgray;
        font-size: 1.51rem;
        cursor: pointer;
      }
    }
  }
}

a {
  color: white;
  text-decoration: none;
}
