.about_section {
  margin: 100px 0;

  .about_sub {
    margin: 0 auto;
    width: 80%;
    height: 60vh;
    overflow: hidden;
    position: relative;

    ul {
      width: 300%;
      font-size: 0;
      margin: 0;
      padding: 0;
      display: flex;
      transition: all ease 1s;

      .content_section {
        margin: 0 auto;
        padding: 0 1%;
        width: 28%;
        height: 60vh;
        font-weight: 800;
        font-style: normal;
      }
      img {
        width: 100%;
        height: 60vh;
      }
    }
  }
}
