.project_section {
  margin: 0;
  padding: 0;

  .project_sub {
    width: 80%;
    margin: 0px auto;

    ul {
      padding: 0;
      font-size: 0;
    }
  }
}

//Comp영역

.comp_section {
  display: inline-block;
  width: 50%;

  a {
    display: block;
    text-decoration: none;
    margin: 5px;

    .screen {
      position: relative;
      overflow: hidden;

      .textDeco {
        background-color: rgba(0, 0, 0, 0.3);
        width: 100%;
        height: 100%;
        position: absolute;
        z-index: 2;

        font-size: 5vw;
        color: #fff;
        font-weight: 700;
        font-style: italic;
        align-items: center;

        display: none;
      }

      img {
        width: 100%;
        transition: all 0.5s ease 0s;
      }
    }

    &:hover .hoverNone {
      display: inline-flex;
      align-items: center;
      justify-content: center;
    }

    &:hover img {
      transform: scale(1.1);
    }
  }
}
