.profile_section {
  margin: 0;
  padding: 0;

  .profile_sub {
    width: 80%;
    margin: 100px auto;
    display: flex;

    img {
      width: 30vw;
      margin-right: 10vw;
    }

    .profile_text {
      width: 50vw;
    }
  }
}

.textStyle {
  margin: 10%;
  span {
    font: inherit;
    font-size: 1.5vw;
    font-weight: 800;
    &:first-child {
      display: inline-block;
      width: 6vw;
      text-align: right;
      margin-right: 3vw;
    }
  }
}
